<template>
  <!-- 简历库 -->
  <div class="jobFairBox" v-loading="loading">
    <div class="container">
      <label class="search-box">
        <input type="text" placeholder="快速搜索简历" />
        <button type="button" @click="search()">搜索</button>
      </label>
      <div class="quicklySearch">
        <ul>
          <li class="title">专业：</li>
          <li
            @click="changeType('search', index)"
            v-for="(item, index) in searchList"
            :key="index"
            :class="searchActive === index ? 'active' : ''"
          >
            {{ item.name }}
          </li>
        </ul>
      </div>
      <ul class="search-list">
        <li>
          <label>学历：</label>
          <div class="content">
            <select v-model="searchForm.qualifications" class="r-select">
              <option
                v-for="(item, key) in professionalData"
                :value="item.value"
                :key="key"
              >
                {{ item.name }}
              </option>
            </select>
            <span>-</span>
            <select class="r-select">
              <option
                v-for="(item, key) in professionalData"
                :value="item.value"
                :key="key"
              >
                {{ item.name }}
              </option>
            </select>
          </div>
        </li>
        <li>
          <label>期望薪资：</label>
          <div class="content">
            <select class="r-select">
              <option
                v-for="(item, key) in professionalData"
                :value="item.value"
                :key="key"
              >
                {{ item.name }}
              </option>
            </select>
            <span>-</span>
            <select class="r-select">
              <option
                v-for="(item, key) in professionalData"
                :value="item.value"
                :key="key"
              >
                {{ item.name }}
              </option>
            </select>
          </div>
        </li>
        <li>
          <label>性别：</label>
          <div class="content">
            <select class="r-select" v-model="searchForm.sex">
              <option
                v-for="(item, key) in sexData"
                :value="item.value"
                :key="key"
              >
                {{ item.label }}
              </option>
            </select>
          </div>
        </li>
        <li>
          <label>年龄要求：</label>
          <div class="content">
            <select class="r-select">
              <option
                v-for="(item, key) in professionalData"
                :value="item.value"
                :key="key"
              >
                {{ item.name }}
              </option>
            </select>
            <span>-</span>
            <select class="r-select">
              <option
                v-for="(item, key) in professionalData"
                :value="item.value"
                :key="key"
              >
                {{ item.name }}
              </option>
            </select>
          </div>
        </li>
        <li>
          <label>工作年限：</label>
          <div class="content">
            <select class="r-select">
              <option
                v-for="(item, key) in professionalData"
                :value="item.value"
                :key="key"
              >
                {{ item.name }}
              </option>
            </select>
            <span>-</span>
            <select class="r-select">
              <option
                v-for="(item, key) in professionalData"
                :value="item.value"
                :key="key"
              >
                {{ item.name }}
              </option>
            </select>
          </div>
        </li>
        <li>
          <label>求职状态：</label>
          <div class="content">
            <select class="r-select">
              <option
                v-for="(item, key) in professionalData"
                :value="item.value"
                :key="key"
              >
                {{ item.name }}
              </option>
            </select>
          </div>
        </li>
      </ul>
    </div>
    <div class="main-content">
      <div v-if="!isSearch" class="search-no-data">
        <h3>搜索方法</h3>
        <ul>
          <li>
            <div class="icon-wrap">
              <img src="../../assets/img/kuaisu.png" alt="" />
            </div>
            <div class="content">
              <h4>更快速</h4>
              <p>在线职位+特定名词搜索</p>
            </div>
          </li>
          <li>
            <div class="icon-wrap">
              <img src="../../assets/img/zhunque.png" alt="" />
            </div>
            <div class="content">
              <h4>更准确</h4>
              <p>可使用推荐词直接搜索</p>
            </div>
          </li>
          <li>
            <div class="icon-wrap">
              <img src="../../assets/img/qiangda.png" alt="" />
            </div>
            <div class="content">
              <h4>更强大</h4>
              <p>可选专业、薪资、年限等</p>
            </div>
          </li>
        </ul>
      </div>
      <template v-if="isSearch">
        <div class="resume-list" v-for="(item, k) in listData.rows" :key="k">
          <div class="resume-info">
            <div class="img-wrap">
              <img :src="item.photoUrl" alt="" />
            </div>
            <div class="info">
              <h4>{{item.name}} <i :class="['sex-icon',item.sex === 0 ? 'girl' : 'boy']"></i></h4>
              <ul class="info-list">
                <li>30岁</li>
                <li>7年</li>
                <li>{{item.qualifications}}</li>
                <li>{{item.workStatus}}</li>
                <li>{{item.reqSalary}}</li>
              </ul>
              <div class="describe">
                {{item.proDesc}}
              </div>
            </div>
          </div>
          <ul class="position-info">
            <li>
              <i class="icon qiwang"></i>
              <label>期望：</label>
              <div class="list">
                <span>{{item.reqCity}}</span>
                <span>{{item.reqIndustry}}</span>
              </div>
            </li>
             <li>
                <i class="icon zhiwei"></i>
                <label>职位：</label>
                <div class="list">
                  <span>武大郎</span>
                  <span>{{item.reqPositon}}</span>
                </div>
              </li>
              <li>
                <i class="icon yuanxiao"></i>
                <label>院校：</label>
                <div class="list">
                  <span>兰州</span>
                  <span>{{item.school}}</span>
                </div>
              </li>
          </ul>
          <div class="opt-bar">
            <el-button size="small" type="primary">联系他</el-button>
            <el-button
              size="small"
              type="primary"
              @click="lookResume(item)"
              >查看简历</el-button
            >
          </div>
        </div>
      </template>
      <template v-if="listData.rows">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="pageInfo.page"
          :page-size="pageInfo.limit"
          layout="prev, pager, next, jumper"
          :total="listData.total"
        >
        </el-pagination>
      </template>
    </div>
  </div>
</template>
<script>
import {getCvList} from '@/api/resume'
export default {
  name: "resume",
  data() {
    return {
      loading:false,
      isSearch:false,
      listData:{},
      searchList: [
        { name: "专业不限" },
        { name: "环境工程" },
        { name: "环境污染与治理" },
        { name: "环境卫生工程" },
        { name: "环境化工" },
        { name: "环境监测" },
        { name: "环境科学" },
        { name: "生态学" },
        { name: "环境保护与监测" },
        { name: "环境规划与管理" },
        { name: "环境工程管理" },
        { name: "规划与环境保护" },
        { name: "环境监察" },
        { name: "环境治理工程" },
      ],
      searchActive: "",
      professionalData: [
        {
          name: "不限",
          value: "0",
        },
        {
          name: "大专",
          value: "1",
        },
      ],
      sexData:[
        {label:'不限',value:''},
        {label:'女',value:0},
        {label:'男',value:1},
      ],
      pageInfo:{
        page:1,
        limit:10
      },
      positionData:[
        {},{}
      ],
      searchForm:{
        qualifications:'',//学历
        reqSalary:'',//期望薪酬
        sex:'',//性别
        speciality:'',//专业
        workStatus:'',//求职状态

      }
    };
  },
  mounted(){
  },
  methods: {
    changeType(item, index) {
      this[item + "Active"] = index;
    },
    handleSizeChange(val) {},
    handleCurrentChange(val) {},
    lookResume(data) {
      this.$router.push({ name: 'resumeDetail',query:{id:data.id} });
    },
    search(){
      this.loading = true;
      let _params = {...this.pageInfo,...this.searchForm};
      this.getList(_params)
    },
    //获取简历列表
    getList(param){
      getCvList(param).then(res=>{
        if(res.code===200) {
          this.listData = res;
          this.loading = false;
          this.isSearch = true;
        }else {
          this.$message.error(res.msg);
        }
      })
    }
  },
};
</script>
<style lang="less" scoped>
@import "../../assets/variable.less";
.search-list {
  &::after {
    content: "";
    display: block;
    height: 0;
    clear: both;
  }
  li {
    display: block;
    float: left;
    width: 40%;
    text-align: left;
    margin-bottom: 10px;
    & > label,
    & > .content {
      display: inline-block;
      vertical-align: middle;
    }
    & > .content {
      width: 380px;
    }
    & > label {
      color: #fc8863;
      width: 80px;
    }
    &:nth-child(3n + 3) {
      width: 20%;
      .content {
        width: 120px;
      }
    }
  }
}
.main-content {
  padding: 20px 0;
}
.search-no-data {
  background: #fff;
  width: 1080px;
  margin: 0 auto 60px;
  padding: 15px;
  text-align: left;
  h3 {
    font-size: 20px;
    color: #7d8394;
    font-weight: 400;
  }
  ul {
    overflow: hidden;
    padding: 15px 0 5px;
  }
  li {
    .float;
    color: #7d8394;
    width: 33.3333%;
  }
  .icon-wrap,
  .content {
    .inline-block;
    h4 {
      font-size: 18px;
      padding: 5px 0;
    }
  }
  .icon-wrap {
    width: 80px;
    height: 80px;
    margin-right: 5px;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
.resume-list {
  width: 1200px;
  box-sizing: border-box;
  margin: 0 auto 15px;
  padding: 20px 30px;
  background: #fff;
  border: 1px #00d7c6 solid;
  overflow: hidden;
}
.resume-info,
.position-info,
.opt-bar {
  .float;
}
.opt-bar {
  width: 135px;
  text-align: right;
  .el-button {
    width: 80px;
  }
}

.resume-info {
  width: 700px;
  .img-wrap {
    width: 50px;
    height: 50px;
    border-radius: 100%;
    overflow: hidden;
  }
  .img-wrap,
  .info {
    .float;
  }
  .info {
    width: 570px;
    margin-left: 20px;
    text-align: left;
    & > h4 {
    }
    .info-list {
      overflow: hidden;
      padding: 5px 0 10px;
      // font-size: 12px;
      li {
        display: block;
        float: left;
        margin-right: 20px;
      }
    }
    .describe {
      font-size: 12px;
    }
  }
}
.sex-icon {
  .inline-block;
  width: 18px;
  height: 18px;
  &.boy {
    background-image: url(../../assets/img/boy.png);
    background-size: 100% 100%;
  }
  &.girl {
    background-image: url(../../assets/img/girl.png);
    background-size: 100% 100%;
  }
}
.position-info {
  text-align: left;
  width: 300px;
  li {
    position: relative;
    display: block;
    padding: 5px 0;
    font-size: 13px;
    label {
      color: #7f7f7f;
    }
    &::before {
      content: "";
      display: block;
      border-left: 1px #00d7c6 dashed;
      position: absolute;
      left: 15px;
      height: 10px;
      bottom: -8px;
    }
    &:last-child {
      &::before {
        display: none;
      }
    }
    .icon,
    label,
    .list {
      .inline-block;
    }
    .icon {
      position: relative;
      z-index: 1;
      width: 30px;
      height: 30px;
      background-size: 100% 100%;
      &.qiwang {
        background-image: url(../../assets/img/qiwang.png);
      }
      &.zhiwei {
        background-image: url(../../assets/img/zhiwei.png);
      }
      &.yuanxiao {
        background-image: url(../../assets/img/yuanxiao.png);
      }
    }
    .list {
      font-size: 0;

      span {
        position: relative;
        font-size: 13px;
        margin-right: 11px;
        .inline-block;
        &::after {
          content: "";
          display: block;
          height: 10px;
          position: absolute;
          top: 50%;
          right: -6px;
          margin-top: -5px;
          border-left: 1px #333 solid;
        }
        &:last-child {
          &::after {
            display: none;
          }
        }
      }
    }
  }
}
</style>


